import { RpJsonError } from "@rp/react/rpjson/types"

enum ExpectedErrors {
  ACCESS_DENIED = 403,
  NOT_FOUND = 404,
}

const errorMessages = {
  [ExpectedErrors.ACCESS_DENIED]: "Access is denied to this document because you are not the owner",
  [ExpectedErrors.NOT_FOUND]: "The document you are trying to open doesn't exist",
}

const getErrorMessage = (error: RpJsonError) => {
  if (error.response?.status === ExpectedErrors.ACCESS_DENIED) {
    return errorMessages[ExpectedErrors.ACCESS_DENIED]
  }
  if (error.response?.status === ExpectedErrors.NOT_FOUND) {
    return errorMessages[ExpectedErrors.NOT_FOUND]
  }

  return String(error)
}

export { getErrorMessage }
